@use './BreakPoints';

@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,700&family=Source+Sans+Pro:ital@1&display=swap');
.Navbar{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 6vh;
    span{
        @media (max-width: #{map-get(BreakPoints.$breakpoints, xs)}){
            margin-left: 10px;
        }
    }
    
}


.logo{
    margin-right: 15px;
    padding: 0 13px;
    font-size: 22px;
    background-color: #eceff1;
    font-family: 'Roboto', sans-serif;
    height: 100%;
    display: flex;
    align-items: center;
    @media (max-width: #{map-get(BreakPoints.$breakpoints, xs)}){
        display: none;
    }

}
.logo a {
    text-decoration: none;
    color: black;
    
}


.slider {
    width: 340px;
    margin: 0 10px;
    display: inline-block;

    @media (max-width: #{map-get(BreakPoints.$breakpoints, lg)}){
        width: 350px;
        
    }
    @media (max-width: #{map-get(BreakPoints.$breakpoints, sm)}){
        width: 250px;
        
    }
    @media (max-width: #{map-get(BreakPoints.$breakpoints, xs)}){
        width: 150px;
        
    } 
}

.rc-slider-track{
    background-color: transparent;
}

.rc-slider-handle,
.rc-slider-handle:active,
.rc-slider-handle:focus,
.rc-slider-handle:hover{
    background-color: green;
    outline: none;
    border: 2px solid green;
    box-shadow: none;
    width: 13px;
    height: 13px;
    margin-left: -7px;
    margin-top: -3px;

}

.rc-slider-rail{
    height: 8px;
}

.select-container{
    margin-left: auto;
    margin-right: 1rem;
}
